.header{
    width: 100%;
    height: 70px;
    background: #090909;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 44px;
    font-size: 14px;
    color: #fff;
}
.header .list{
    font-size: 13px;
    line-height: 70px;
    color: #fff;
    display: flex;
    gap: 21px;
}
.header .list li{
    cursor: pointer;
}
.header .list li:hover{
    text-decoration: underline;
    text-underline-position: under;
}
.header .menu{
    display: flex;
    align-items: center;
    line-height: 24px;
    gap: 21px;
    color: #fff; 
}
.header .menu .profill{
    width: 24px;
    height: 24px;
}
.header h1{
    margin-right: 45px;
}
.header h1 img{
    width: 100px;
    height: 30px;
    margin-top: 20px;
}
.header .icon_wrap{
    display: flex;
    align-items: center;
    gap: 25px;
    color: #fff;
}
.profill{
    width: 32px;
}
svg,img,.kids{
    cursor: pointer;
}
.header .menu .bell{
    position: relative;
}
.header .menu .bell::after{
    content: '4';
    width: 18px;
    height: 18px;
    background: #811312;
    color: #fff;
    font-weight: bold;
    position: absolute;
    right: -10px;
    top: 25%;
    transform: translateY(-50%);
    border-radius: 18px;
    text-align: center;
    line-height: 18px;
}
