.footer{
    width: 100%;
    background: #141414;
    padding: 130px 400px 30px;
    color: rgba(255,255,255,0.5);
}
.footer .container{
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: flex-start;
}
.footer .container .sns{
    display: flex;
    gap: 30px;
    cursor: pointer;
}
.footer .container .fgnb_wrap{
    display: flex;
    gap: 100px;
}
.footer .container .fgnb_wrap .fgnb li{
    font-size: 14px;
    line-height: 30px;
    cursor: pointer;
}
.footer .container address, .container .copy{
    font-size: 12px;
    line-height: 1.3;
    font-style: normal
}
.footer .container .code{
    color: rgba(255, 255, 255,0.5);
    padding: 8px;
    border: 1px solid rgba(255, 255, 255,0.5);
}